.box-tag-topic{
    margin-bottom: 32px;
    .container{
        padding-top: 27px; padding-right: 0;
    }
    &__list {
        width: 100%; 
        overflow-x: scroll;
        @include hide-scrollbar;
        &-inner {
            white-space: nowrap;
        }
    }
    .tag{
        border: 1px solid var(--border);
        border-radius: 2px;
        height: 44px;line-height: 44px;display: inline-block;
        padding: 0 16px;
        font-size: 16px;font-weight: 400;margin-right: 10px;
        background: #FAF1ED;
        &:last-child {
            margin-right: 24px;
        }
    }
}