body.dark_mode {
    --bg-agray2: #121212;
    --white: #222;
    --border: #282828;
    --colormain: #fff;
    --gray1: #e5e5e5;
    --bg-agray1: #222;
    background: var(--bg-agray2);

    .wrap-main-nav {
        border-top: 1px solid var(--border);
        background: var(--bg-agray2);
    }

    .box_comment_vne {
        --border: rgba(255, 255, 255, 0.36);
    }
}

.title_star {
    font: bold 24px/1.4 var(--font2);color: #282828;margin-bottom: 20px;
    position: relative;
    background: url(images/icons/star1.svg)no-repeat 0;
    padding-left: 18px;
    a{
        padding: 0 15px 0 10px;
    }
    &.line{
        a{
            color: #282828;position: relative;
        }
    }
}

// ================================

@mixin hide-scrollbar {
    &::-webkit-scrollbar {
        display: none;
    }
}

.d-none {
    display: none !important;
}

.d-flex {
    display: flex;
}

.d-block {
    display: block;
}

.d-inline-block {
    display: inline-block;
}

.border-bottom {
    border-bottom: 1px solid #E5E5E5;
}

.box-placeholder{
    .thumb-art{
        .thumb{
            opacity: .1;
            animation: fading 1.5s infinite;
        }
    }
    .number{
        background: var(--colormain);
        height: 20px;
        width: 25px;
        opacity: .1;
        animation: fading 1.5s infinite;
    }
    .title-news,.description,.status{
        background: var(--colormain);
        opacity: .1;
        animation: fading 1.5s infinite;
    }
    .title-news{height: 30px;}
    .description{
        height: 50px;width: 85%;
    }
    .status{
        height: 20px;width: 60%;
    }
}