.banner-top-home {
    background: var(--bg-agray2);
    display: flex;
    width: 100%;
    justify-content: center;
    transition: none 0s ease 0s;
}

.wrap-main-nav {

    .main-nav {
        max-width: 100%;
        overflow-x: scroll;
        // @include hide-scrollbar;
        background: #F6F7FB;

        .parent {
            display: flex;
            white-space: nowrap;

            li {
                display: inline-block;

                a {
                    text-transform: uppercase;
                    font: 600 14px var(--font1);
                    color: var(--colormain);
                    padding: 19px 16px;
                    display: inline-block;
                }
            }
        }
    }

    .search-btn {
        display: inline-block;
        cursor: pointer;
        height: 19px;
        position: relative;

        input {
            display: none;
        }

        button {
            border: 0;
            background: none;
        }

        .icon-svg {
            position: relative;
            display: block;
            top: 7px;
            -webkit-transition-duration: 400ms;
            transition-duration: 400ms;
            -webkit-transition-property: all;
            transition-property: all;
            -webkit-transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
            transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
        }

        .close {
            width: 11px;
            height: 11px;
            opacity: 0;
        }

        &.is-clicked {
            .icon-svg {
                top: -13px;
                opacity: 0;
            }

            .close {
                opacity: 1;
                top: -6px;
            }
        }
    }

    #myvne_taskbar {
        display: inline-block;
        margin-left: 25px;
    }
}

.search-wrap {
    background: #fff;
    display: none;
    box-shadow: 0 6px 12px rgba(0, 0, 0, .1);
    -moz-box-shadow: 0 6px 12px rgba(0, 0, 0, .1);

    input {
        font-size: 18px;
        height: 45px;
        width: 100%;
        outline: 0;
    }
}

.header-top {
    
    .flex {
        height: 56px;
        justify-content: space-between; 
        align-items: center;
    }

    .icon-svg {
        width: 18px; height: 18px;
    }
}